
.header{position:absolute;top:0;left:0;width:100%;height:232px;background:url("../assets/bg.png") no-repeat center -10px;position:relative;margin:-42px auto 0;cursor:pointer
}
.header .h-content .h-info{position:relative;margin:auto;width:1160px;height:170px
}
.header .h-content .logo{position:absolute;width:220px;height:105px;left:24px;top:55px;background:transparent no-repeat left center;z-index:100;background-image:url("../assets/logo.png")
}
.header .h-content .banner-title{position:absolute;top:114px;left:255px;line-height:20px;padding:6px 10px;background-color:#000;background-color:rgba(0,0,0,0.68);color:#fff;border-radius:4px;font-size:14px;max-width:350px;visibility:hidden;opacity:0;transition:.2s;cursor:pointer
}
.header .h-content .search{position:absolute;top:114px;right:0;width:268px;height:32px;padding:2px 2px 2px 72px;background-color:#e5e9ef;background-color:rgba(0,0,0,0.12);border-radius:6px
}
.header .h-content .search .link-ranking{position:absolute;left:2px;top:2px;height:32px;line-height:32px;background-color:#fff;background-color:rgba(255,255,255,0.88);border-radius:4px;width:68px;transition:.2s background-color
}
.header .h-content .search .link-ranking span{font-size:12px;padding-left:26px;color:#f25d8e;display:inline-block;background:url("../assets/icons.png") -659px -655px no-repeat
}
.header .h-content .search form{background-color:#fff;background-color:rgba(255,255,255,0.88);display:block;height:32px;border-radius:4px;transition:.2s background-color
}
.header .h-content .search input{float:left;width:200px;color:#222;font-size:12px;overflow:hidden;height:32px;line-height:32px;padding:0 12px;border:0;box-shadow:none;background-color:transparent;outline:inherit;font-size:12px;font-family:"Microsoft YaHei",Arial,Helvetica,sans-serif
}
.header .h-content .search button{display:block;position:absolute;right:0;width:48px;min-width:0;cursor:pointer;height:32px;background:url("../assets/icons.png") -653px -720px;margin:0;padding:0;border:0;outline:inherit
}
.header .h-content .search button:hover{background:url("../assets/icons.png") -718px -720px
}
.header .h-content:hover .banner-title{animation:in .2s linear;opacity:1;visibility:visible
}
@keyframes in{
0%{opacity:0;transform:scale(0.95)
}
30%{transform:scale(1)
}
70%{transform:scale(1.01)
}
100%{opacity:1;transform:scale(1)
}
}
.header .h-menu{width:100%;background:#fff
}
.header .h-menu .menu-wrapper{margin:auto;width:1160px;height:62px;padding:6px 0
}
.header .h-menu .menu-wrapper ul li{float:left;position:relative;font-size:14px;display:block;height:50px;padding-right:20px
}
.header .h-menu .menu-wrapper ul li a{display:inline-block;width:28px;height:50px
}
.header .h-menu .menu-wrapper ul li a span.menu-text{text-align:center;display:inline-block;padding-top:23px
}
.header .h-menu .menu-wrapper ul li a:hover span{color:#00a1d6
}
.header .h-menu .menu-wrapper ul li .dashboard{background:url("../assets/icons.png") -658px -1170px no-repeat
}
.header .h-menu .menu-wrapper ul li.other-menu{width:62px;padding-right:0
}
.header .h-menu .menu-wrapper ul li.other-menu a{width:62px;text-align:center
}
.header .h-menu .menu-wrapper ul li.other-menu .menu-num{position:absolute;top:8px;left:0;height:14px;width:100%;text-align:center
}
.header .h-menu .menu-wrapper ul li.other-menu .menu-num span.num{position:static;display:inline-block;vertical-align:top;top:auto;left:auto;margin-left:0;margin-top:-1px;background-color:#ffafc9;color:#fff;border-radius:4px;height:13px;line-height:12px;font-size:11px;min-width:18px;padding:1px 3px 2px 3px;transform:scale(0.9)
}
.header .h-menu .menu-wrapper ul li.last-menu{float:left;position:relative;font-size:14px;display:block;height:50px;margin-left:20px;padding-right:12px
}
.header .h-menu .menu-wrapper ul li.last-menu a{width:40px;padding-left:22px;padding-right:0;padding-top:0;line-height:50px;margin:0 !important;font-size:16px
}
.header .h-menu .menu-wrapper ul li.last-menu a.square{background:url("../assets/icons.png") -664px -904px no-repeat
}
.header .h-menu .menu-wrapper ul li.last-menu a.live{background:url("../assets/icons.png") -664px -518px no-repeat
}
@media screen and (max-width: 1400px){
.header .h-content .h-info{width:980px
}
.header .h-menu .menu-wrapper{width:980px
}
.header .h-menu .menu-wrapper ul li{padding-right:12px
}
.header .h-menu .menu-wrapper ul li.other-menu{width:50px
}
.header .h-menu .menu-wrapper ul li.other-menu a{width:50px
}
}



